// 初始化
$(function() {
  onFeatureHover()
  onProductsHover()
})

// 监听特色hover状态
function onFeatureHover() {
  $('.index__feature-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetFeatureHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}

// 重置特色hover状态
function resetFeatureHover() {
  $('.index__feature-item').removeClass('hover')
}

// 监听产品hover状态
function onProductsHover() {
  $('.index__products-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetProductsHover()
      $(this).addClass('hover')
      var icon = $(this).children('.index__products-icon')
      icon.attr('src', icon.data('hover'))
      changeAttach($(this))
    }
  })
}

// 重置产品hover状态
function resetProductsHover() {
  $('.index__products-item').removeClass('hover')
  $('.index__products-icon').each(function() {
    $(this).attr('src', $(this).data('normal'))
  })
}

// 切换视频/图片资源
function changeAttach(item) {
  var parentClass = item.prop('className').split(' ')[0].split('-')[0]
  $('.' + parentClass + '-media').hide()
  $('.' + parentClass + '-media').each(function() {
    if($(this).data('index') === item.data('index')) {
      $(this).show()
    }
  })
}
